import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';

import color from './colors.json';

global.text_primary = color.text_primary;
global.text_primary_light = color.text_primary_light;
global.accent = color.accent;
global.accent_secondary = color.accent_secondary;
global.accent_dark = color.accent_dark;
global.outline = color.outline;
global.background_dark = color.background_dark;
global.background_light = color.background_light;

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);
